<template>
  <div class="addActivity mescroll">
    <div class="mid">
      <div class="headPic">
        <img class="pic" :src="detail.aimg?$utils.dealImg(detail.aimg):require('@/assets/img/default/defaultH.png')" alt />
        <img class="icon" src="@/assets/img/merchantsIcon/edit.png" @click="upload('1')" alt />
        <input ref="upload1" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
      </div>
      <van-cell-group>
        <van-field v-model="detail.aname" input-align="right" clearable label="活动名称" placeholder="请输入活动名称，便于自己管理" @blur="blur()" />
        <van-field v-model="detail.viewName" input-align="right" clearable label="活动标题" placeholder="展示给用户看的活动标题内容" @blur="blur()" />
        <van-field v-model="detail.viewName2" input-align="right" clearable label="活动副标题" placeholder="展示给用户看的副标题内容" @blur="blur()" />
        <van-field v-model="detail.amusic" input-align="right" clearable label="配乐" placeholder="音乐网址，可不填" @blur="blur()" />
        <van-field v-model="detail.amarketPrice" type="number" input-align="right" clearable label="原价" placeholder="请输入商品或服务的原价" @blur="blur()" />
        <van-field v-model="detail.aoutPrice" type="number" input-align="right" clearable label="活动价" placeholder="请输入本次活动的特价" @blur="blur()" />
        <van-field v-model="detail.acounts" type="digit" input-align="right" clearable label="总库存" placeholder="本次活动限购的总数量" @blur="blur()" />
        <van-field v-model="detail.afanh1" type="number" input-align="right" clearable label="一级返佣" placeholder="用户推荐好友购买后返还的佣金" @blur="blur()" />
        <van-field v-model="detail.afanh2" type="number" input-align="right" clearable label="二级返佣" placeholder="好友的好友购买后返还用户的佣金" @blur="blur()" />
        <van-cell title="开始时间" :value-class="detail.beginDate?'color':''" is-link :value="detail.beginDate||'活动正式开始的时间'" @click="beginDateShow = true" />
        <van-cell title="结束时间" :value-class="detail.endDate?'color':''" is-link :value="detail.endDate||'活动截止时间'" @click="endDateShow = true" />
        <van-field v-model="detail.aredTel" input-align="right" clearable type="tel" label="客服电话" placeholder="请输入您的客服电话" @blur="blur()" />
        <van-field v-model="detail.aredMoneyEvery" type="number" input-align="right" clearable label="单个红包最大额" placeholder="发给用户单个红包的最大金额" @blur="blur()" />
        <van-field v-model="detail.aredMoneyMax" type="number" input-align="right" clearable label="红包最大额" placeholder="发送红包总金额最大值" @blur="blur()" />
        <div>
          <van-cell @click="upload('2')">
            <template slot="title">
              <span class="custom-text">详细介绍</span>
            </template>
            <div class="content">
              <p>点击上传活动介绍图片</p>
              <img class="icon" src="@/assets/img/merchantsIcon/fujian.png" alt />
              <input ref="upload2" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
            </div>
          </van-cell>
          <div class="filesList" v-if="picList.length">
            <div v-for="(item,index) in picList" :key="index" class="filesItem">
              <img :src="$utils.dealImg(item)" alt />
              <span class="del" @click="delImg(index)">×</span>
            </div>
          </div>
        </div>
      </van-cell-group>
      <div class="submit-wrap">
        <div class="submit" @click="submit()">提交</div>
      </div>
    </div>
    <van-popup v-model="beginDateShow" position="bottom">
      <van-datetime-picker v-model="currentDate" type="datetime" @confirm="beginDateConfirm" @cancel="beginDateShow = false" />
    </van-popup>
    <van-popup v-model="endDateShow" position="bottom">
      <van-datetime-picker v-model="currentDate" type="datetime" @confirm="endDateConfirm" @cancel="endDateShow = false" />
    </van-popup>
  </div>
</template>
<script>
import { CellGroup, Cell, Field, Popup, DatetimePicker } from "vant";
import areaList from "@/utils/AreaList";
import { getActivityDetail, saveActivityDetail } from "@/api";
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker
  },
  data() {
    return {
      type: "",
      detail: {
        acode: "",
        aname: "",
        viewName: "",
        viewName2: "",
        aimg: "",
        amusic: "",
        amarketPrice: "",
        aoutPrice: "",
        acounts: "",
        afanh1: "",
        afanh2: "",
        beginDate: "",
        endDate: "",
        aredTel: "",
        aredMoneyEvery: ""
      },
      beginDateShow: false,
      endDateShow: false,
      currentDate: new Date(),
      picList: []
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    beginDateConfirm(value) {
      this.detail.beginDate = this.getValueDate(value);
      this.beginDateShow = false;
    },
    endDateConfirm(value) {
      this.detail.endDate = this.getValueDate(value);
      this.endDateShow = false;
    },
    getValueDate(value) {
      let year = new Date(value).getFullYear(),
        month = new Date(value).getMonth() + 1,
        day = new Date(value).getDate(),
        hour = new Date(value).getHours(),
        min = new Date(value).getMinutes(),
        sec = new Date(value).getSeconds();
      return (
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":00"
      );
    },
    delImg(index) {
      this.picList.splice(index, 1);
    },
    upload(type) {
      this.type = type;
      if (this.type == 2 && this.picList.length > 8) {
        this.$toast("最多只能上传9张图片");
        return;
      }
      this.$refs["upload" + this.type].click();
    },
    chooseUpload(e) {
      let that = this,
        formData = new FormData(),
        files = e.target.files,
        reader = new FileReader();
      console.log(e);
      console.log(files.length);

      if (files.length < 1) {
        return;
      }
      this.loading = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: "加载中..."
      });
      for (let i = 0; i < files.length; i++) {
        console.log(files[i]);
        let size = files[i].size / 1024 / 1024;
        if (size > 100) {
          // 超过2M
          this.$toast.fail("文件大于100M");
          return;
        }
        reader.readAsDataURL(files[i]);
        reader.onload = function() {
          let content = this.result; //图片的src，base64格式
          let img = new Image();
          img.src = content;
          img.onload = function() {
            //图片加载完毕
            that.compressEvent(img, size, dataURL => {
              that.uploadImg(dataURL, files[i], i, files.length);
            });
          };
        };
      }
    },
    compressEvent(img, size, callback) {
      let that = this,
        canvas = document.createElement("canvas"),
        ctx = canvas.getContext("2d"),
        initSize = img.src.length,
        width = img.width,
        height = img.height;
      // var ratio = this.getPixelRatio(ctx);
      // console.log(width * ratio, height * ratio);
      canvas.width = width;
      canvas.height = height;
      ctx.drawImage(img, 0, 0, width, height);
      let dataURL = canvas.toDataURL("image/jpeg", size > 1 ? 0.1 : 1);
      callback ? callback(dataURL) : null; //调用回调函数
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    dataURItoBlob(dataurl) {
      //dataurl是base64格式
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    uploadImg(dataURL, file, index, length) {
      // let size = file.size / 1024 / 1024;
      // if (size > 50) {
      //   // 超过2M
      //   this.$toast.fail("文件大于50M");
      //   this.loading.clear();
      //   return;
      // }
      let blob = this.dataURItoBlob(dataURL),
        formData = new FormData(),
        config = {
          headers: { "Content-Type": "multipart/form-data" }
        };
      formData.append("file", blob, file.name);
      formData.append("path", 1);
      this.$axios
        .post(
          this.$config.host + "/api/common/upload/file",
          formData,
          config
        )
        .then(res => {
          if (index + 1 == length) {
            this.loading.clear();
          }
          console.log(res);
          if (res.code == 0) {
            if (this.type == "1") {
              this.detail.aimg = res.data.filename;
            } else {
              this.picList.push(res.data.filename);
            }
          } else {
            this.$toast.fail("图片上传失败！");
          }
        })
        .catch(err => {
          this.$toast.clear();
          console.log(err);
          this.$toast.fail("网络错误");
        });
    },
    submit() {
      if (!this.detail.aname) {
        this.$toast("请输入活动名称");
        return;
      }
      if (!this.detail.viewName) {
        this.$toast("请输入活动标题");
        return;
      }
      if (!this.detail.viewName2) {
        this.$toast("请输入活动副标题");
        return;
      }
      if (!this.detail.aimg) {
        this.$toast("请上传首图");
        return;
      }
      // if (!this.detail.amusic) {
      //   this.$toast("请输入配乐地址");
      //   return;
      // }
      // if (!this.$utils.isMp3(this.detail.amusic)) {
      //   this.$toast("配乐地址不正确");
      //   return;
      // }
      if (!this.detail.amarketPrice) {
        this.$toast("请输入原价");
        return;
      }
      if (!this.detail.aoutPrice) {
        this.$toast("请输入活动价");
        return;
      }
      if (!this.detail.acounts) {
        this.$toast("请输入总库存");
        return;
      }
      if (!this.detail.afanh1) {
        this.$toast("请输入一级返佣");
        return;
      }
      if (!this.detail.afanh2) {
        this.$toast("请输入二级返佣");
        return;
      }
      if (!this.detail.beginDate) {
        this.$toast("请选择开始时间");
        return;
      }
      if (!this.detail.endDate) {
        this.$toast("请选择结束时间");
        return;
      }
      if (
        new Date(this.detail.beginDate).getTime() >=
        new Date(this.detail.endDate).getTime()
      ) {
        this.$toast("开始时间不能晚于结束时间");
        return;
      }
      if (!this.detail.aredTel) {
        this.$toast("请输入客服电话");
        return;
      }
      if (!this.$utils.isTelno(this.detail.aredTel)) {
        this.$toast("客服电话格式不正确");
        return;
      }
      if (!this.detail.aredMoneyMax) {
        this.$toast("请输入红包最大额");
        return;
      }
      if (this.detail.aredMoneyMax < 10) {
        this.$toast("红包最大额不能小于10");
        return;
      }
      if (!this.detail.aredMoneyEvery) {
        this.$toast("请输入单个红包最大额");
        return;
      }
      if (this.detail.aredMoneyEvery < 2) {
        this.$toast("单个红包最大额不能小于2");
        return;
      }
      this.detail.amsg = this.picList.join(",");
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      saveActivityDetail({
        ...this.detail
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast(this.$route.query.acode ? "修改成功" : "新增成功");
          if (this.$route.query.acode) {
            setTimeout(() => {
              this.$router.push({
                name: "shareActivity",
                query: {
                  id: res.data
                }
              });
            });
          } else {
            setTimeout(() => {
              this.$router.push({
                name: "addResult",
                query: {
                  id: res.data
                }
              });
            });
          }
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getActivityDetail({
        acode: this.$route.query.acode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = {
            acode: res.data.acode,
            aname: res.data.aname,
            viewName: res.data.viewName,
            viewName2: res.data.viewName2,
            aimg: res.data.aimg,
            amusic: res.data.amusic,
            amarketPrice: res.data.amarketPrice,
            aoutPrice: res.data.aoutPrice,
            acounts: res.data.acounts,
            afanh1: res.data.afanh1,
            afanh2: res.data.afanh2,
            beginDate: res.data.beginDate,
            endDate: res.data.endDate,
            aredTel: res.data.aredTel,
            aredMoneyMax: res.data.aredMoneyMax,
            aredMoneyEvery: res.data.aredMoneyEvery
          };
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title = "活动";
    if (this.$route.query.acode) {
      this.loadData();
    }
  }
};
</script>
<style lang="scss" scoped>
.addActivity {
  .mid {
    background: #fff;
    padding: 10px 0;
    .headPic {
      width: 200px;
      margin: 5px auto 15px;
      position: relative;
      .pic {
        width: 200px;
        max-height: 150px;
      }
      .icon {
        position: absolute;
        width: 25px;
        height: 25px;
        right: 0px;
        bottom: 0px;
      }
    }
    .content {
      display: flex;
      align-items: center;
      p {
        color: #333;
        flex: 2;
      }
      .icon {
        width: 20px;
        height: 20px;
        margin-left: 5px;
      }
    }
    .filesList {
      padding: 10px 15px;
      background: #fff;
      .filesItem {
        position: relative;
        border-bottom: 1pt solid #efefef;
        width: 30%;
        display: inline-block;
        margin-right: 3%;
        img {
          width: 100%;
        }
        .del {
          position: absolute;
          border: 1pt solid red;
          color: red;
          border-radius: 50%;
          width: 15px;
          height: 15px;
          text-align: center;
          line-height: 15px;
          right: -8px;
          top: -8px;
        }
      }
      p {
        word-break: break-all;
        line-height: 30px;
        margin: 0 30px 0 0;
      }
      .del {
        position: absolute;
        border: 1pt solid red;
        color: red;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        text-align: center;
        line-height: 15px;
        right: 0;
        top: 5px;
      }
    }
    .submit {
      width: 90%;
      margin: 10px auto 0;
      text-align: center;
      background: $base-color;
      color: white;
      height: 45px;
      line-height: 45px;
      border-radius: 10px;
      margin-bottom: 10px;
    }
  }
  .color {
    color: #000;
  }
}
</style>
<style lang="scss">
.addActivity {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell__value {
    flex: 2;
  }
}
</style>